nav {
  display: block;
  width: 400px;
  height: 100vh;
  top: 0;
  left: 0;
  position: fixed;
}

nav .poly1 {
  width: 100%;
  height: 100vh;
  background-color: var(--secondary);
  -webkit-clip-path: polygon(0 0, 20% 0, 80% 100%, 0% 100%);
          clip-path: polygon(0 0, 20% 0, 80% 100%, 0% 100%);
  position: absolute;
  z-index: 10;
}

nav .poly2 {
  width: 100%;
  height: 100vh;
  background-color: #DD5151;
  -webkit-clip-path: polygon(0 0, 100% 0, 50% 100%, 0% 100%);
          clip-path: polygon(0 0, 100% 0, 50% 100%, 0% 100%);
  position: absolute;
  z-index: 20;
}

nav .poly3 {
  width: 100%;
  height: 100vh;
  background: #2c2e3a;
  background: -webkit-gradient(linear, left bottom, left top, from(var(--header1)), to(var(--header2)));
  background: linear-gradient(0deg, var(--header1) 0%, var(--header2) 100%);
  -webkit-clip-path: polygon(0 0, 96% 0, 50% 100%, 0% 100%);
          clip-path: polygon(0 0, 96% 0, 50% 100%, 0% 100%);
  position: absolute;
  z-index: 30;
}

nav .navContent {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  padding: 10% 0 10% 15%;
  position: absolute;
  z-index: 40;
}

@media only screen and (max-width: 1000px) {
  nav {
    display: none;
    width: 30%;
