.inputContainer {
    box-sizing: border-box;
    background-color: var(--bw100);
    width: 100%;
    height: 35px;
    padding: 5px;
    border-radius: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 1px 1px 2px #00000099;
    cursor: pointer;

    .inputLabel {
        height: 100%;
        display: flex;
        justify-content: flex-end;
        align-items: center;

        p {
            color: var(--text);
            text-align: right;
            font-size: 0.7rem;
            font-weight: 700;
        }
        
        img {
            height: 80%;
            width: auto;
            margin: 0px 5px;
        }
    }

}