:root {
    --primary: #E6E6E6;
    --secondary: #4D6780;
    --tertiary: #324E67;
    --comp1: #DD5151;
    --comp2: #1C344A;
    
    --header1: #8EB1D2;
    --header2: #324E67;

    --primaryTitle: #324E67;
    --disablePrimaryTitle: #324E6799;
    --secondaryTitle: #4D6780;
    --tertiaryTitle: #ffffff;
    --text: #4D6780;

    --button1: #E79533;
    --button2: #F3D156;
    --activeButton1: #EDBF89;
    --activeButton2: #FEF0BE;
    --buttonTittle: #00000066;


    --bw100: #ffffff;
    --bw90: #E6E6E6;
    --bw80: #CCCCCC;
    --bw70: #CCCCCC;
    --bw60: #B3B3B3;
    --bw50: #999999;
    --bw0: #000000;


    --font_08: 0.8rem;
}

body {
  margin: 0;
  background-color: var(--primary);
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a {    
  text-decoration: none;
}

img {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

button {
  user-select: none;
}

.mt-1 {
  margin-top: 10px;
}

    /* width */
    ::-webkit-scrollbar {
      width: 5px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
      background-color: transparent;
      margin: 30px 0px;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
      background-color:#BFBFBF;
      border-radius: 50px;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background-color: #B3B3B3;
    border-radius: 50px;
  }